body {
  font-size: 16px;
  line-height: 18px;
  .content {
    .firstBlock {
      height: 877px;
      background-image: url("../img/first_block_bg.jpg");
      background-size: 100% 100%;
      .firstBlock__contacts {
        margin: 120px 0 0 75px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        ul{
          display: flex;
          flex-direction: column;
          align-items: center;
          li {
            p,
            a {
              text-transform: uppercase;
            }
            &:nth-child(1) {
              p {
                font: 2.75em/1em "HeliosCondC";
                color: #fff;
              }
            }
            &:nth-child(2) {
              a {
                font: 3em/1.5em "HeliosCond-Bold";
                color: #fff;
                text-decoration: none;
              }
            }
            &:nth-child(3) {
              p {
                font: 1.5em/.5em "HeliosCond-Bold";
                color: #fff;
              }
            }
          }
        }
      }
      .firstBlock__info {
        margin: 95px 0 0;
        li {
          list-style: disc;
          color: #bf172a;
          p {
            font: 1.25em/2em "HeliosCond-Bold";
            color: #bf172a;
          }
        }
      }
      button {
        margin: 20px 0 0;
        width: 425px;
        height: 90px;
        font: 4em/90px "AdigianaUltra";
        color: #dc0038;
        text-shadow: 5px 0 10px #fff, 0 5px 10px #fff, -5px 0 10px #fff, 0 -5px 10px #fff;
        background-color: #2c9195;
        border: none;
        border-radius: 50px;
        outline: none;
        box-shadow: 7px 0 30px #fff, 0 7px 30px #fff, -7px 0 30px #fff, 0 -7px 30px #fff;
      }
    }
    .secondBlock {
      height: 884px;
      background-image: url("../img/second_block_bg.jpg");
      background-size: 100% 100%;
      .secondBlock__headline {
        margin: 70px 0 0 -140px;
        font: 3.25em/1em "AdigianaUltra";
        color: #fff;
        text-align: center;
        transform: rotate(-4deg);
      }
      .secondBlock__video {
        margin: 110px 0 0 60px;
        width: 800px;
        height: 500px;
        background-color: #11808a;
        border-radius: 50px;
        box-shadow: 7px 0 10px #fff, 0 7px 10px #fff, -7px 0 10px #fff, 0 -7px 10px #fff;
        overflow: hidden;
        iframe{
          width: 100%;
          height: 100%;
        }
      }
    }
    .thirdBlock{
      height: 884px;
      background-image: url("../img/third_block_bg.jpg");
      background-size: 100% 100%;
      .thirdBlock__content{
        .thirdBlock__headline{
          margin: 125px 0 0;
          display: flex;
          p{
            font: 5em/1em "AdigianaUltra";
            color: #15828c;
            text-align: center;
            text-shadow: 7px 0 20px #fff, 0 7px 20px #fff, -7px 0 20px #fff, 0 -7px 20px #fff;
            transform: rotate(-4deg);
          }
        }
        .thirdBlock__info{
          margin: 0 0 0 25px;
          li {
            margin: 50px 0;
            list-style: disc;
            color: #bf172a;
            p {
              font: 1.875em/2em "HeliosCond-Bold";
              color: #bf172a;
            }
          }
        }
      }
    }
    .fourthBlock{
      height: 884px;
      background-image: url('../img/fourth_block_bg.jpg');
      background-size: 100% 100%;
      .fourthBlock__headline{
        margin: -60px 0 0;
        p{
          font: 3.875em/4em "LuckiestGuy-Regular";
          color: #fff;
          text-transform: uppercase;
        }
      }
      .category{
        display: flex;
        justify-content: space-around;
        .debutant,
        .nominee,
        .superstar{
          width: 26%;
          h6{
            margin: 40px 0 0;
            position: relative;
            font: 3.125em/100px "LuckiestGuy-Regular";
            color: #fed900;
            text-align: center;
            text-transform: uppercase;
            text-shadow: 5px 5px #00858e;
            background: #00a19b;
            background: -moz-linear-gradient(left, #00a19b 0%, #00a3a1 17%, #00adb6 33%, #65c3d3 50%, #00adb6 67%, #00a3a1 83%, #00a19b 100%);
            background: -webkit-linear-gradient(left, #00a19b 0%,#00a3a1 17%,#00adb6 33%,#65c3d3 50%,#00adb6 67%,#00a3a1 83%,#00a19b 100%);
            background: linear-gradient(to right, #00a19b 0%,#00a3a1 17%,#00adb6 33%,#65c3d3 50%,#00adb6 67%,#00a3a1 83%,#00a19b 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00a19b', endColorstr='#00a19b',GradientType=1 );
            border-radius: 10px;
            &:before{
              content: url("../img/camera.png");
              position: absolute;
              top: -55px;
              left: -45px;
            }
            &:after{
              content: url("../img/star.png");
              position: absolute;
              bottom: -60px;
              right: -20px;
            }
          }
          ul{
            margin: 20px 0 0 20px;
            list-style: disc;
            color: #fff;
            li {
              p {
                font: 1.5em/1.5em "HeliosCond-Bold";
                color: #fff;
              }
            }
          }
        }
      }
      button {
        margin: -40px 0 0;
        width: 425px;
        height: 90px;
        font: 4em/90px "AdigianaUltra";
        color: #dc0038;
        text-shadow: 5px 0 10px #fff, 0 5px 10px #fff, -5px 0 10px #fff, 0 -5px 10px #fff;
        background-color: #2c9195;
        border: none;
        border-radius: 50px;
        outline: none;
        box-shadow: 7px 0 30px #fff, 0 7px 30px #fff, -7px 0 30px #fff, 0 -7px 30px #fff;
      }
    }
    .fifthBlock{
      height: 884px;
      background-image: url("../img/fifth_block_bg.jpg");
      background-size: 100% 100%;
      .fifthBlock__headline{
        margin: 35px 0 0;
        h6{
          font: 3.75em/1em "AdigianaUltra";
          color: #15828c;
          text-shadow: 10px 0 20px #fff, 0 10px 20px #fff, -10px 0 20px #fff, 0 -10px 20px #fff;
          transform: rotate(-4deg);
        }
        span{
          margin: 125px 0 0;
          display: block;
          font: 2.5em/1em "HeliosCond-Bold";
          color: #fff;
        }
      }
      .fifthBlock__foto{
        ul{
          margin: 95px 0 0;
          display: flex;
          justify-content: space-around;
          li{
            width: 100%;
            display: flex;
            justify-content: center;
            img{
              width: 80%;
              height: 250px;
              border: 5px solid #fff;
              border-radius: 25px;
            }
          }
        }
      }
      .fifthBlock__footer{
        ul{
          margin: 50px 0 0 90px;
          li{
            p{
              transform: rotate(-4deg);
            }
            &:nth-child(1){
              p{
                font: 12em/1em "AdigianaUltra";
                color: #f10f0f;
                text-shadow: 10px 0 20px #fff, 0 10px 20px #fff, -10px 0 20px #fff, 0 -10px 20px #fff;
              }
            }
            &:nth-child(2){
              text-align: right;
              p{
                font: 7em/1em "AdigianaUltra";
                color: #f10f0f;
                text-shadow: 10px 0 20px #fff, 0 10px 20px #fff, -10px 0 20px #fff, 0 -10px 20px #fff;
              }
            }
          }
        }
      }
    }
    .sixthBlock{
      height: 884px;
      background-image: url("../img/sixth_block_bg.jpg");
      background-size: 100% 100%;
      .sixthBlock__headline{
        margin: 30px 0 0;
        h6{
          font: 7.5em/1em "AdigianaUltra";
          color: #15828c;
          text-shadow: 10px 0 20px #fff, 0 10px 20px #fff, -10px 0 20px #fff, 0 -10px 20px #fff;
          transform: rotate(-4deg);
        }
      }
      .sixthBlock__info{
        margin: 30px 0 0;
        ul{
          margin: 110px 0 0;
          li{
            margin: 40px 0;
            p{
              width: 480px;
              display: flex;
              justify-content: space-between;
              align-items: baseline;
              font: 2em/1em "HeliosCond-Bold";
              color: #c1171e;
            }
            &:nth-child(1){
              span{
                font-size: 2em;
              }
            }
            &:nth-child(2){
              span{
                font-size: 2em;
                color: #15828c;
                text-shadow: 5px 0 10px #fff, 0 5px 10px #fff, -5px 0 10px #fff, 0 -5px 10px #fff;
              }
            }
          }
        }
        .adult{
          margin: 80px 0 0;
          font: 1.875em/1em "HeliosCond-Bold";
          color: #15828c;
          text-shadow: 5px 0 10px #fff, 0 5px 10px #fff, -5px 0 10px #fff, 0 -5px 10px #fff;
        }
      }
    }
    .seventhBlock{
      height: 906px;
      background-image: url("../img/seventh-block_bg.jpg");
      background-size: 100% 100%;
      .seventhBlock__contacts {
        margin: 100px 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        li {
          p,
          a {
            text-transform: uppercase;
          }
          &:nth-child(1) {
            p {
              font: 2.5em/1em "HeliosCondC";
              color: #fff;
            }
          }
          &:nth-child(2) {
            a {
              font: 2.75em/1.5em "HeliosCond-Bold";
              color: #fff;
              text-decoration: none;
            }
          }
          &:nth-child(3) {
            p {
              font: 1.25em/.5em "HeliosCond-Bold";
              color: #fff;
            }
          }
        }
      }
      .seventhBlock__logo {
        margin: 65px 0 0;
      }
      .seventhBlock__map{
        position: relative;
        height: 500px;
        div{
          position: absolute;
          ul{
            position: relative;
            background: #fff;
            box-sizing: border-box;
            border-radius: 10px;
            box-shadow: 5px 5px 20px #fff, -5px -5px 20px #fff, 5px -5px  20px #fff, -5px 5px 20px #fff;
            li{
              &:nth-child(1){
                p{
                  font: 1.5em/1em "AdigianaUltra";
                  color: #ce2622;
                }
              }
              &:nth-child(2){
                p{
                  font: 1em/1em "HeliosCond-Bold";
                  color: #000;
                }
              }
              &:nth-child(3){
                p{
                  font: 1em/1em "HeliosCondC";
                  color: #000;
                }
              }
            }
            &:after{
              content: "";
              position: absolute;
              width: 0;
              height: 0;
              margin: 0 0 0 -.5em;
              box-sizing: border-box;
              border: 1em solid #fff;
              border-color: transparent transparent #fff #fff;
              border-radius: 5px;
              transform-origin: 0 0;
              box-shadow: -5px 5px 20px #fff;
            }
          }
        }
        .lvov,
        .zaporozhye,
        .poltava{
          ul{
            li{
              &:nth-child(2){
                p{
                  font-family: HeliosCondC;
                }
              }
            }
          }
        }
        .lvov,
        .khmelnytckyi,
        .poltava,
        .kharkov,
        .zaporozhye,
        .odessa{
          ul{
            &:after{
              top: 0;
              left: 60px;
              transform: rotate(135deg);
            }
          }
        }
        .kyiv{
          top: -60px;
          left: 26%;
          ul{
            li{
              &:nth-child(even){
                font: 1em/1em "HeliosCond-Bold";
              }
              &:nth-child(odd){
                font: 1em/1em "HeliosCondC";
              }
            }
            &:after{
              top: 120px;
              left: 220px;
              transform: rotate(-45deg);
            }
          }
        }
        .chernigov{
          top: -40px;
          left: 57%;
          ul{
            &:after{
              top: 1px;
              left: 8px;
              transform: rotate(45deg);
            }
          }
        }
        .khmelnytckyi{
          top: 135px;
          left: 25%;
          ul{
            &:after{

            }
          }
        }
        .kharkov{
          top: 140px;
          left: 73%;
        }
        .lvov{
          top: 100px;
          left: 12%;
        }
        .dnepropetrovsk{
          top: 240px;
          left: 51%;
          ul{
            &:after{
              top: 0;
              left: 190px;
              transform: rotate(135deg);
            }
          }
        }
        .odessa{
          top: 320px;
          left: 42%;
        }
        .zaporozhye{
          top: 310px;
          left: 70%;
        }
        .poltava{
          top: 120px;
          left: 60%;
        }
      }
      button {
        margin: -40px 0 0;
        width: 425px;
        height: 90px;
        font: 4em/90px "AdigianaUltra";
        color: #fff;
        background-color: #df0040;
        border: none;
        border-radius: 50px;
        outline: none;
        box-shadow: 7px 0 30px #fff, 0 7px 30px #fff, -7px 0 30px #fff, 0 -7px 30px #fff;
      }
      .seventhBlock__totalOrder{
        margin: -140px 0 0;
        p{
          font: 3.25em/2em "HeliosCondC";
          color: #fff;
          text-transform: uppercase;
        }
        label{
          input{
            width: 275px;
            height: 90px;
            font: 5em/90px "HeliosCond-Bold";
            color: #df0040;
            text-align: center;
            border: none;
            border-radius: 50px;
            outline: none;
          }
        }
      }
    }
  }
}